.outer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

.chat-container {
    display: flex;
    flex-direction: column;
    height: 95%;
    width: 95%;
    border: 2px solid #0077b5; /* Slightly thicker border with a different color */
    padding: 15px;
    background-color: #f9f9f9; /* Light background for the whole chat container */
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2); /* Adding a shadow for depth */
}

.message-list {
    flex-grow: 1;
    overflow-y: auto;
    padding: 5px;
    background-color: #ffffff; /* Separate background for messages */
    border-radius: 15px; /* Rounded corners for message list */
}

.message {
    margin: 10px;
    padding: 15px;
    border-radius: 15px;
    max-width: 70%;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Shadow for individual messages */
}

.message.user {
    margin-left: auto;
    background-color: #e6f7ff; /* Light blue for user messages */
}

.message.assistant {
    background-color: #fff0f6; /* Light pink for assistant messages */
}

.input-container {
    display: flex;
    margin-top: 10px; /* Added margin to top */
}

.input-container input {
    flex-grow: 1;
    margin-right: 10px;
    padding: 12px;
    border: 2px solid #0077b5;
    border-radius: 15px; /* Rounded input field */
}

.input-container button {
    padding: 12px 20px; /* More padding for better aesthetics */
    border: none;
    border-radius: 15px;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
    font-weight: bold; /* Bold text on button */
}

.loading {
    text-align: center;
    font-style: italic;
    color: #888888;
}
